@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  font-family: "Barlow Semi Condensed", sans-serif;
  background: white;
  background: radial-gradient(circle, hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  text-align: center;
}

/* FEUILLE */
.paper {
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  border: 18px solid;
  box-shadow: inset 0px 5px 6px grey;
  height: 130px;
  width: 130px;
  border-radius: 50%;
  background-image: url(/static/media/icon-paper.8b57a6b1.svg);
  border-color: hsl(230, 89%, 62%);
}
.paper:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
@media (min-width: 768px) {
  .paper {
    background-size: 45%;
    height: 200px;
    width: 200px;
  }
}
@media (max-height: 668px) {
  .paper {
    background-size: 45%;
    height: 100px;
    width: 100px;
  }
}

/* CISEAUX */
.scissors {
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  border: 18px solid;
  box-shadow: inset 0px 5px 6px grey;
  height: 130px;
  width: 130px;
  border-radius: 50%;
  background-image: url(/static/media/icon-scissors.3b1a5d7e.svg);
  border-color: hsl(39, 89%, 49%);
}
.scissors:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
@media (min-width: 768px) {
  .scissors {
    background-size: 45%;
    height: 200px;
    width: 200px;
  }
}
@media (max-height: 668px) {
  .scissors {
    background-size: 45%;
    height: 100px;
    width: 100px;
  }
}

/* PIERRE */
.rock {
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  border: 18px solid;
  box-shadow: inset 0px 5px 6px grey;
  height: 130px;
  width: 130px;
  border-radius: 50%;
  background-image: url(/static/media/icon-rock.476e90a9.svg);
  border-color: hsl(349, 71%, 52%);
}
.rock:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
@media (min-width: 768px) {
  .rock {
    background-size: 45%;
    height: 200px;
    width: 200px;
  }
}
@media (max-height: 668px) {
  .rock {
    background-size: 45%;
    height: 100px;
    width: 100px;
  }
}

.icon.paper,
.icon.scissors,
.icon.rock {
  align-self: center;
  margin-bottom: 28px;
}

.paper.winner,
.scissors.winner,
.rock.winner {
  box-shadow: inset 0px 8px 8px grey,
    0px 0px 0px 20px rgba(253, 253, 253, 0.082),
    0px 0px 0px 50px rgba(253, 253, 253, 0.082),
    0px 0px 0px 80px rgb(253, 253, 253, 0.082);
  z-index: -10;
}

@media (min-width: 768px) {
  .icon {
    height: 294px;
    width: 294px;
  }
  .icon.rock {
    border: 30px solid;
    border-color: hsl(349, 71%, 52%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .icon.paper {
    border: 30px solid;
    border-color: hsl(230, 89%, 62%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .icon.scissors {
    border: 30px solid;
    border-color: hsl(39, 89%, 49%);
    box-shadow: inset 0px 8px 8px grey;
    cursor: pointer;
  }
  .paper.winner,
  .scissors.winner,
  .rock.winner {
    box-shadow: inset 0px 8px 8px grey, 0px 0px 0px 50px rgba(45, 51, 80, 0.7),
      0px 0px 0px 100px rgba(38, 46, 74, 0.7),
      0px 0px 0px 150px rgb(32, 39, 70, 0.7);
    z-index: -10;
  }
}


